body {
  background-color: rgba(50, 50, 50, 0.1);
}

#question-area {
  text-align: center;
}

#question-area-class {
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.class-choice {
  width: 40vw;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 3px 3px 5px black;
  transition: 0.5s;
  background: white;
}
.class-choice:hover {
  /*transform: scale(1.05);*/
  filter: invert(1);
  cursor: pointer;
  /*color: white;*/
  /*background-color: black;*/
}

#display-area {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.class-card {
  /*background-color: white;*/
  width: 20vw;
  font-family: serif;
  font-size: 20px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 2px 2px 5px black;  
  padding: 10px;
  margin: 10px;
}